import Vue from 'vue'
import App from './App.vue'
import { BootstrapVue, BootstrapVueIcons } from 'bootstrap-vue'
import VueObserveVisibility from 'vue-observe-visibility'
import router from './router'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faFacebookSquare, faInstagram, faYoutube } from '@fortawesome/free-brands-svg-icons'
import { faInfoCircle, faWifi, faMugHot, faClock, faArchive, faPrint } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

library.add(faFacebookSquare, faInstagram, faYoutube, faInfoCircle, faWifi, faMugHot, faClock, faArchive, faPrint)

Vue.use(BootstrapVue)
Vue.use(BootstrapVueIcons)
Vue.use(VueObserveVisibility)
Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
